@import 'fonts'; // Шрифты
@import 'reset'; // Сброс стилей
@import 'setting'; // Общие настройки
@import 'vars'; // Переменные
@import 'mixins'; // Миксины


// Компоненты 
@import 'components/footer';
@import 'components/header';


body {
    font-family: 'Montserrat', sans-serif;
}



// Стили



.main-screen {
    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 60px;
        background: linear-gradient(180deg, #E6F5FF 0%, #F3F8FB 69.8%, rgba(243, 248, 251, 0) 100%);
        border-radius: 16px;

        @media(max-width: 768px) {
            flex-direction: column-reverse;
            padding: 15px;
        }
    }

    &__content {
        margin-right: 50px;
        margin-top: -50px;

        @media(max-width: 768px) {
            margin-top: 15px;
        }

        h1 {
            font-weight: 700;
            font-size: 28px;
            line-height: 140%;
            color: #303133;

            @media(max-width: 768px) {
                font-size: 24px;
            }
        }

        p {
            font-weight: 500;
            font-size: 15px;
            line-height: 180%;
            color: #303133;
            margin-top: 20px;
            max-width: 450px
        }
    }

    &__img {
        max-width: 350px;

        @media(max-width: 768px) {
            margin-bottom: 20px;
        }

        img {
            width: 100%;
            object-fit: cover;
        }
    }

    .timer {
        display: flex;
        padding: 17px;
        background: rgba(255, 255, 255, 0.6);
        border-radius: 16px;
        width: max-content;
        margin-bottom: 25px;

        div {
            display: flex;
            flex-direction: column;
            align-items: center;
            position: relative;
            min-width: 40px !important;
            max-width: 40px !important;
        }

        svg {
            margin: 0px 6px;
            margin-top: 12px;
        }

        .number {
            font-style: normal;
            font-weight: 600;
            font-size: 26px;
            line-height: 32px;
            color: #303133;
        }

        .title {
            font-weight: 500;
            font-size: 12px;
            line-height: 15px;
            color: #303133;
            text-transform: uppercase;
        }
    }
}


.video-block {
    &__inner {
        display: flex;

        @media(max-width: 768px) {
            flex-direction: column;
            align-items: center;
            margin-top: 40px;

            position: relative;

            // &::before {
            //     content: '';
            //     position: absolute;
            //     width: 100%;
            //     height: 80px;
            //     bottom: 0;
            //     left: 0;
            //     background: linear-gradient(0deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
            // }
        }
    }

    .video {
        min-width: 70%;
        margin-right: 30px;
        position: relative;

        .play-btn {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-image: url("data:image/svg+xml,%3Csvg width='48' height='49' viewBox='0 0 48 49' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M24 48.1C30.3652 48.1 36.4697 45.6136 40.9706 41.1877C45.4714 36.7619 48 30.7591 48 24.5C48 18.2409 45.4714 12.2381 40.9706 7.81228C36.4697 3.38642 30.3652 0.900002 24 0.900002C17.6348 0.900002 11.5303 3.38642 7.02944 7.81228C2.52856 12.2381 0 18.2409 0 24.5C0 30.7591 2.52856 36.7619 7.02944 41.1877C11.5303 45.6136 17.6348 48.1 24 48.1ZM22.665 16.1456C22.2132 15.8492 21.688 15.6789 21.1455 15.653C20.6031 15.6271 20.0636 15.7465 19.5847 15.9985C19.1058 16.2504 18.7055 16.6255 18.4264 17.0837C18.1473 17.5418 17.9999 18.0659 18 18.6V30.4C17.9999 30.9341 18.1473 31.4582 18.4264 31.9163C18.7055 32.3745 19.1058 32.7496 19.5847 33.0015C20.0636 33.2535 20.6031 33.3729 21.1455 33.347C21.688 33.3211 22.2132 33.1508 22.665 32.8544L31.665 26.9544C32.0759 26.685 32.4128 26.32 32.6458 25.8919C32.8788 25.4637 33.0007 24.9856 33.0007 24.5C33.0007 24.0144 32.8788 23.5363 32.6458 23.1081C32.4128 22.68 32.0759 22.315 31.665 22.0456L22.665 16.1456Z' fill='white'/%3E%3C/svg%3E%0A");
            background-size: cover;
            width: 48px;
            height: 48px;
        }

        @media(max-width: 768px) {
            min-width: 100%;
            margin-right: 0;
            margin-bottom: 20px;
        }

        img {
            width: 100%;
            object-fit: cover;
        }
    }

    &__content {
        overflow-y: auto;
        max-height: 367px;
        scrollbar-width: thin;

        @media(max-width: 768px) {
            max-height: 100%;

        }

        .name {
            font-weight: 700;
            font-size: 16px;
            line-height: 180%;
            color: #303133;

            @media(max-width: 768px) {
                font-size: 13px;
            }
        }

        p {
            font-weight: 400;
            font-size: 14px;
            line-height: 180%;
            color: #303133;
            padding-right: 15px;

            @media(max-width: 768px) {
                font-size: 13px;
            }
        }


        /* width */
        &::-webkit-scrollbar {
            width: 5px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            box-shadow: inset 0 0 5px #EDF7FD;
            border-radius: 5px;
        }

        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: rgba(180, 195, 207, 0.61);
            border-radius: 5px;
        }
    }
}






.stock {
    margin-top: 90px;

    @media(max-width: 768px) {
        margin-top: 50px;
    }

    &__inner {
        padding: 40px 30px;
        background: #F4F8FB;
        border-radius: 16px;

        @media(max-width: 768px) {
            padding: 15px;
        }

        &-row {
            display: flex;
            margin-top: 30px;

            @media(max-width: 768px) {
                flex-direction: column;
            }

            .stock-col {
                width: 50%;
                display: flex;

                @media(max-width: 768px) {
                    width: 100%;
                    flex-direction: column;
                    margin-bottom: 30px;
                }

                .stock-text {
                    font-weight: 400;
                    font-size: 15px;
                    line-height: 180%;
                    color: #303133;
                }

                .stock-img {
                    margin-right: 20px;

                    @media(max-width: 768px) {
                        margin-right: 0;
                        margin-bottom: 10px;
                    }
                }

                &:first-child {
                    margin-right: 25px;
                }
            }
        }
    }

    &-title {
        font-weight: 700;
        font-size: 28px;
        line-height: 140%;
        color: #303133;

        @media(max-width: 768px) {
            font-size: 24px;
        }
    }


}



.benefit {
    margin-top: 60px;
    padding-bottom: 20px;

    &-title {
        font-weight: 700;
        font-size: 28px;
        line-height: 140%;
        color: #303133;

        @media(max-width: 768px) {
            margin-bottom: 25px;
        }
    }

    &-items {
        display: flex;
        align-items: center;

        @media(max-width: 768px) {
            flex-direction: column;
        }
    }

    &-item {
        width: 50%;
        margin-top: 25px;

        @media(max-width: 768px) {
            width: 100%;
            padding: 16px;
            margin-top: 10px;
            display: flex;
            align-items: center;
        }

        &:first-child {
            margin-right: 45px;

            @media(max-width: 768px) {
                margin-right: 0;
            }
        }

        background: linear-gradient(61.54deg, #1C5CF1 15.1%, #54F5CF 108.48%);
        border-radius: 16px;
        padding: 23px;

        svg {
            margin-right: 25px;

            @media(max-width: 768px) {
                width: 20px;
                height: 16px;
                margin-right: 15px;
            }
        }

        span {
            font-weight: 600;
            font-size: 20px;
            line-height: 180%;
            color: #FFFFFF;

            @media(max-width: 768px) {
                font-size: 15px;
                line-height: 140%;
            }
        }
    }
}


.blue-btn {
    font-weight: 700;
    font-size: 15px;
    line-height: 180%;
    color: #FFFFFF;
    padding: 10px 35px;
    width: max-content;
    background: #3A75FF;
    border-radius: 15px;
    margin-top: 30px;
    display: block;
    transition: 0.2s linear;

    &:hover {
        box-shadow: 0px 12px 18px rgba(58, 117, 255, 0.4);
        background: #3A75FF;
    }
}


.stock-for-all {
    margin-top: 60px;

    @media(max-width: 768px) {
        margin-top: 40px;
    }

    &__inner {
        display: flex;

        @media(max-width: 768px) {
            flex-direction: column;
        }

        .stock-col {
            width: 50%;

            @media(max-width: 768px) {
                width: 100%;

                &:first-child {
                    margin-right: 0;
                    margin-bottom: 45px;
                }
            }

            .stock-progress {
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-radius: 15px;
                margin-bottom: 10px;

                @media(max-width: 768px) {
                    padding: 15px;
                }

                .progress-text {
                    font-weight: 400;
                    font-size: 15px;
                    line-height: 140%;
                    text-transform: uppercase;
                    color: #303133;

                    @media(max-width: 768px) {
                        font-size: 13px;
                    }
                }

                .progress-percent {
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 140%;
                    text-transform: uppercase;
                    color: #303133;

                    @media(max-width: 768px) {
                        font-size: 13px;
                    }
                }

                padding: 20px;
            }

            .col-title {
                font-weight: 700;
                font-size: 20px;
                line-height: 140%;
                color: #303133;
                margin-bottom: 30px;
            }

            .stock-list {
                li {
                    font-weight: 400;
                    font-size: 15px;
                    line-height: 180%;
                    color: #303133;
                    margin-bottom: 20px;

                    span {
                        font-weight: bold;
                    }
                }
            }

            .blue-btn {


                @media(max-width: 768px) {
                    width: 100% !important;
                    margin-top: 40px;
                    text-align: center;
                }


            }

            .hint {
                font-weight: 400;
                font-size: 12px;
                line-height: 180%;
                color: #303133;
                margin-top: 30px;

                @media(max-width: 768px) {
                    margin-top: 10px;
                }
            }

            &:first-child {
                margin-right: 45px;

                @media(max-width: 768px) {
                    .blue-btn {
                        display: none !important;
                    }

                    .hint {
                        display: none !important;
                    }
                }
            }

            &:last-child {
                @media(min-width: 768px) {
                    .blue-btn {
                        display: none !important;
                    }

                    .hint {
                        display: none !important;
                    }
                }
            }
        }
    }
}



.copy-link {
    position: relative;

    &::before {
        position: absolute;
        content: 'Ссылка скопирована';
        background: #555;
        padding: 10px 15px;
        width: max-content;
        border-radius: 5px;
        color: #fff;
        top: -45px;
        left: 50%;
        transform: translateX(-50%) translateY(20px);
        font-size: 12px;
        opacity: 0;
        transition: 0.1s linear;
    }
}

.copy-link.visible {
    &::before {
        transform: translateX(-50%) translateY(0px);
        opacity: 1;
    }

}

.examples {
    margin-top: 100px;

    @media(max-width: 768px) {
        margin-top: 40px;
    }

    &__inner {
        display: flex;
        align-items: center;

        @media(max-width: 768px) {
            flex-direction: column;
        }
    }

    &__item {
        width: 50%;
        background: #FFFFFF;
        border: 1px solid #F7F7F7;
        border-radius: 16px;
        padding: 30px;
        filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.04)) drop-shadow(0px 8px 16px rgba(0, 0, 0, 0.08));

        @media(max-width: 768px) {
            width: 100%;
            padding: 15px;
        }

        &:first-child {
            margin-right: 26px;

            @media(max-width: 768px) {
                margin-right: 0;
                margin-bottom: 20px;
            }
        }

        .title {
            font-weight: 700;
            font-size: 20px;
            line-height: 24px;
            color: #303133;
        }

        .text {
            font-weight: 400;
            font-size: 15px;
            line-height: 180%;
            color: #303133;
            margin-top: 20px;

            @media(max-width: 768px) {
                margin-top: 15px;
                font-size: 13px;
            }
        }
    }
}



.faq {
    margin-top: 90px;

    &-title {
        font-weight: 700;
        font-size: 20px;
        line-height: 140%;
        color: #303133;
        margin-bottom: 40px;

    }

    .ac {
        background: #F3F9FC;
        border-radius: 16px;
        border: none;
        transition: 0.2s linear;
        font-family: 'Montserrat', sans-serif;

        .ac-trigger {
            border: none;
            border-radius: none;
            padding: 21px;
            padding-left: 60px;
            border: none;
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            color: #303133;
            font-family: 'Montserrat', sans-serif;

            @media(max-width: 768px) {
                font-size: 15px !important;
                padding: 15px;
                padding-left: 45px;
            }

            &:after {
                content: '';
                right: auto;
                left: 15px;
                background-image: url("data:image/svg+xml,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 8.5V17M17 17V25.5M17 17H25.5M17 17H8.5' stroke='%233A75FF' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                width: 34px;
                height: 34px;
                background-size: cover;

                @media(max-width: 768px) {
                    left: 5px;
                }
            }
        }

        .ac-text {
            font-weight: 400;
            font-size: 15px;
            line-height: 180%;
            color: #303133;
            padding-left: 60px;
            padding-top: 0;
            padding-bottom: 30px;
            font-family: 'Montserrat', sans-serif;

            @media(max-width: 768px) {
                font-size: 13px !important;
                padding-left: 45px;
            }
        }
    }

    .ac.is-active>.ac-header .ac-trigger::after {
        content: '';
        background-image: url("data:image/svg+xml,%3Csvg width='34' height='34' viewBox='0 0 34 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M25.5 17H17H8.5' stroke='%234EC5DE' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") !important;
    }

    .ac.is-active {
        background: #EDF7FE;
    }


}


.ref {
    margin-top: 70px;
    margin-bottom: 50px;

    @media(max-width: 768px) {
        margin-top: 50px;
    }

    &__inner {

        .text {
            font-weight: 600;
            font-size: 20px;
            line-height: 160%;
            color: #303133;

            @media(max-width: 768px) {
                font-size: 15px;
            }
        }

        .ref-btns {
            display: flex;
            align-items: center;
            margin-top: 30px;

            @media(max-width: 768px) {
                flex-direction: column;
                margin-top: 20px;
            }

            .blue-btn {
                margin-right: 40px;
                margin-top: 0;

                @media(max-width: 768px) {
                    width: 100%;
                    text-align: center;
                    margin-right: 0;
                    margin-bottom: 20px;
                }
            }

            .ref-link {
                font-weight: 700;
                font-size: 13px;
                line-height: 140%;
                color: #3A75FF;
                display: flex;
                align-items: center;
                position: relative;
                transition: 0.2s linear;

                &:hover {
                    opacity: 0.6;
                }

                span {
                    padding-bottom: 2px;
                    border-bottom: 1px dashed #3A75FF;
                }


                svg {
                    margin-right: 5px;
                }
            }
        }
    }
}

.progress {
    margin-top: 40px;

    &-title {
        font-weight: 700;
        font-size: 44px;
        line-height: 140%;
        color: #303133;
        position: relative;
        width: max-content;

        @media(max-width: 768px) {
            font-size: 40px;
        }

        &::before {
            content: '$';
            display: block;
            font-weight: 400;
            font-size: 24px;
            line-height: 140%;
            color: #303133;
            position: absolute;
            right: -20px;
            top: 5px;

            @media(max-width: 768px) {
                font-size: 18pxx;
            }
        }
    }

    &-wrapper {
        margin: 5px 0px;
        background: #EDF7FD;
        box-shadow: inset 4px 4px 10px rgba(80, 123, 180, 0.1);
        border-radius: 23px;
        width: 100%;
        height: 45px;
        padding: 5px;

    }

    &-line {
        background: linear-gradient(89.97deg, #3A75FF 2.99%, #5697EF 26.59%, #40FFE8 99.97%);
        border-radius: 17px;
        height: 100%;
    }

    .numbers {
        display: flex;
        align-items: center;
        justify-content: space-between;

        span {
            font-weight: 700;
            font-size: 24px;
            line-height: 140%;
            color: #303133;
            position: relative;

            @media(max-width: 768px) {
                font-size: 20px;
            }

            &::after {
                content: '$';
                font-weight: 400;
                font-size: 24px;
                line-height: 140%;
                color: #303133;
                padding-left: 5px;

                @media(max-width: 768px) {
                    font-size: 20px;
                }
            }
        }


    }



    .progressbar--text {
        margin-top: 70px;
        font-weight: 400;
        font-size: 28px;
        line-height: 140%;
        color: #303133;

        @media(max-width: 768px) {
            font-size: 24px;
            margin-top: 40px;
        }
    }

}

